@import 'reset';
@import 'mixins';
@import 'vars';
@import 'generated/sprite-svg';
@import 'libs/slick';
@import 'libs/slick-theme';
html,
body {
  height: 100%;
  box-sizing: border-box;
  font-size: 20px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

.wrapper {
  width: 100%;
  font-family: $font-primary;
}
img {
  max-width: 100%;
}
.icon {
  fill: #1d363b;
}
.c_sand {
  color: #dfc15e;
}
.section {
  position: relative;
}
.container.container--wide {
  @media (min-width: 992px) {
    max-width: 1366px;
  }
  @media (min-width: 768px) {
    max-width: 1366px;
  }
}
// HEADER
.header {
  padding: 8px 0;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: #ffffff;
  .icon-main-logo {
    width: 190px;
  }
  .navbar {
    max-width: 1790px;
    margin: 0 auto;
  }
  &__logo {
    .icon-main-logo {
      font-size: 2em;
    }
  }
  .main-menu {
    @media only screen and (max-width: 992px) {
      border-top: 1px solid #fff;
      border-bottom: 1px solid #fff;
    }
  }
  .nav-item {
    &:not(:first-child) {
      @media only screen and (min-width: 992px) {
        margin-left: 1.5em;
      }
    }
  }
  .nav-link {
    font-size: 16px;
    padding: 0.2em 0.5em;
    color: #1d363b;
    line-height: 1.31;
    transition: all 0.3s ease;
    &:hover {
      color: #030607;
    }
  }
  .nav-item--cart {
    margin-left: 0.5em !important;
    border-left: solid 1px #e1e1e1;
    .nav-link span {
      color: #e1e1e1;
    }
  }
}
.navbar-toggler {
  border: none;
  color: #1d363b;
  &:focus {
    outline: none !important;
  }
}
.navbar-toggler-icon {
  width: 1.315em;
  height: 2em;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 26.325 19'%3E%3Cdefs%3E%3Cstyle%3E.a,.b,.d{fill:none;}.a,.b{stroke:%231d363b;}.b{stroke-linejoin:round;}.c{stroke:none;}%3C/style%3E%3C/defs%3E%3Cg transform='translate(-323 -14)'%3E%3Cline class='a' x1='9.132' transform='translate(331.596 20.5)'/%3E%3Cline class='a' x1='9.132' transform='translate(331.596 26.5)'/%3E%3Cg class='b' transform='translate(323 14)'%3E%3Crect class='c' width='26.325' height='19'/%3E%3Crect class='d' x='0.5' y='0.5' width='25.325' height='18'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

.carousel-wrapper {
  overflow: hidden;
  .slick-next {
    right: 25px;
  }
  .slick-prev {
    left: 25px;
    z-index: 1;
  }
}

.map-canvas {
  width: 100%;
  height: 300px;
}

picture {
  source,
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.bg-absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: grey;
  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

img {
  border: 0;
}

img:not([src]):not([srcset]) {
  visibility: hidden;
}

.lazyload {
  animation-name: fade;
  animation-duration: 1s;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

a {
  transition: all 0.3s ease;
  text-decoration: none;
}
.form-control:focus {
  box-shadow: none;
  border-color: #1d363b;
}
/* Button Primary */
.btn-primary {
  color: #fff;
  background-color: #1d363b;
  border-color: #1d363b;
  font-family: $font-primary;
  font-weight: 600;
  font-size: 16px;
}
.btn-primary:hover {
  color: #fff;
  background-color: #284b52;
  border-color: #284b52;
}
.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle {
  background-color: #d3e6e9;
  border-color: #d3e6e9;
}
.btn-primary.focus,
.btn-primary:focus,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: none;
}
/* Button Outline Primary */
.btn-outline-primary {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 178px;
  height: 40px;
  line-height: 1;
  color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 16px;
  border: 2px solid rgba(235, 235, 235, 0.8);
  border-radius: 0px;
  padding: 0.5rem 1rem;
}
.btn-outline-primary:hover {
  background-color: #112226;
  border-color: #112226;
}
.btn-outline-primary:not(:disabled):not(.disabled).active,
.btn-outline-primary:not(:disabled):not(.disabled):active,
.show > .btn-outline-primary.dropdown-toggle {
  background-color: #112226;
  border-color: #112226;
}
.btn-outline-primary.focus,
.btn-outline-primary:focus,
.btn-outline-primary:not(:disabled):not(.disabled).active:focus,
.btn-outline-primary:not(:disabled):not(.disabled):active:focus,
.show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: none;
}
/* End of Button Outline Primary */
/* Header */
.header__current-time {
  flex-grow: 1;
  @media only screen and (max-width: 992px) {
    display: none;
  }
}
.time-country {
  font-family: $font-primary;
  font-size: 16px;
  margin-bottom: 0;
  color: #1d363b;
  font-weight: 400;
  display: inline-block;
  margin-top: 12px;
}
.time-country img {
  width: 13px;
  margin: 0px 10px;
}
.time-country span {
  font-family: $font-primary;
  font-style: italic;
}
/* End of Header */
/* Slider */
.slick-slide {
  height: auto;
}
.slide {
  position: relative;
  padding-top: 8rem;
}
.slide__text {
  padding-left: 5vw;
  color: #fff;
}
.slide__subtitle {
  margin-bottom: 1rem;
  font-size: 2rem;
}

.slide__title {
  margin-bottom: 2rem;
  font-family: $font-primary;
  font-size: 2.5rem;
  font-weight: 600;
  line-height: 1.1;
}
.slide__text .btn {
  margin-top: 1rem;
}
.slick-dots {
  position: absolute;
  bottom: 15%;
  left: 5vw;
  margin: 0;
  text-align: left;
}

.slick-dots li {
  padding: 0;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  width: 13px;
  height: 13px;
  margin: 0;
  opacity: 0.44;
  background-color: #ffffff;
  &:before {
    display: none;
  }
}
.slick-dots li.slick-active button {
  opacity: 1;
}
.owl-theme .owl-dots .owl-dot {
  outline: none;
}
.slick-dotted.slick-slider {
  margin-bottom: 0;
}
/* End of Slider */
/* Newly Arrived */
.newly-arrived {
  margin: 3rem 0;
  padding: 3rem 0 0;
  position: relative;
  .content {
    padding-left: 20px;
  }
}
.light-bg {
  background: #f8f8f8;
  width: 95%;
  height: 80%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.content {
  color: #1d363b;
  &__title {
    font-family: $font-primary;
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  &__text {
    font-family: $font-secondary;
    font-size: 1rem;
  }
}
.newly-arrived-box {
  margin-top: 2rem;
  cursor: pointer;
  position: relative;
  transition: all 0.35s;
  &:hover {
    .newly-arrived-box-hover {
      opacity: 1;
    }
  }
}
.newly-arrived-box .img-fluid {
  display: block;
  margin: 0 auto;
  transition: all 0.35s;
}
.newly-arrived-box-hover {
  display: none;
}

.newly-arrived-box-hover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  opacity: 0;
  transition: all 0.3s ease;
  padding: 15px;
}
.newly-arrived-box-hover a {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 700;
  color: #fff;
  line-height: normal;
  font-size: 1rem;
}
.newly-arrived-box-hover a:hover {
  text-decoration: none;
}
.newly-arrived-box-hover hr {
  border-top: 1px solid #707070;
  margin: 8px 0px;
}
.newly-arrived-box-hover p {
  font-family: $font-primary;
  font-size: 18px;
  color: #fff;
  margin-bottom: 0;
}
.newly-arrived-box-hover-1 {
  background: url('../img/newly-arrived/hover.jpg') no-repeat;
  background-size: cover;
}
.compare-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.compare-box a img {
  margin-right: 11px;
}
.compare-box a {
  font-family: $font-primary;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 10px;
  color: #fff;
}
.compare-box a:hover {
  text-decoration: none;
}
.compare-box a span {
  width: 16px;
  height: 16px;
  display: inline-block;
  text-align: center;
  line-height: 14px;
  font-size: 14px;
  border: 1px solid #ebebeb;
  margin-right: 8px;
}
.compare-box .price {
  float: right;
  font-size: 24px;
  font-weight: 300;
  font-family: 'Source Sans Pro', sans-serif;
  color: #fff;
}

.newletter-box {
  max-width: 649px;
  margin: 3rem auto 5rem;
}
.newletter-box h2 {
  font-size: 30px;
  font-weight: 400;
  font-family: 'Lora', serif;
  color: #1d363b;
  line-height: 30px;
  margin-bottom: 35px;
}

.newletter-box .form-control {
  font-family: 'Muli', sans-serif;
  font-weight: 200;
  font-size: 1rem;
  height: 50px;
  border-radius: 0;
}

.newletter-box .btn-primary {
  background: #1d363b url('../img/icons/btn-left-arrow.svg') no-repeat scroll;
  background-position: 98%;
  background-size: 40% 40%;
  padding-right: 70px;
  padding-left: 40px;
  border-radius: 0;
}

.newletter-box .form-control::-webkit-input-placeholder {
  color: #1d363b;
}
.newletter-box .form-control::-moz-placeholder {
  color: #1d363b;
}
.newletter-box .form-control:-ms-input-placeholder {
  color: #1d363b;
}
.newletter-box .form-control:-moz-placeholder {
  color: #1d363b;
}
/* End of Newly Arrived */

/* Two Big ADD */
.two-big-add {
  margin: 5rem 0px 8rem;
}
.two-big-add-col {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 90vh;
  margin-top: 1rem;
}
.two-big-add-col .overlay {
  background: rgba(29, 54, 59, 0.6);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
}
.two-big-add-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  padding: 10vh 5% 15vh 15%;
  z-index: 2;
  @media only screen and (max-width: 992px) {
    padding: 10vh 5% 15vh 5%;
  }
}

.two-big-add-content h2 {
  font-family: $font-primary;
  font-weight: 600;
  color: #fff;
  font-size: 2.4rem;
  line-height: 1.2;
  text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
}

.two-big-add-content p {
  text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
  color: #fff;
  font-family: 'Lora', serif;
  font-weight: 400;
  font-size: 30px;
  margin: 20px 0px 30px;
}

.two-big-add-content h2 span {
  color: #ad974f;
}

.two-big-add-content-second p {
  font-size: 50px;
  font-weight: 400;
  font-family: 'Lora', serif;
  max-width: 95%;
}

/* End of  Two Big ADD */
/* More Product */
.more-product {
  background-repeat: no-repeat;
  background-size: cover;
  padding: 7rem 0px 3.5rem;
  color: #fff;
  .content {
    color: inherit;
  }
}
.more-product-header {
  padding-left: 5%;
}
.product-box {
  margin-top: 1rem;
}
.more-product-info a {
  text-transform: uppercase;
  font-weight: 700;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1rem;
  line-height: 1.25;
  color: #fff;
}

.more-product-info .product-info,
.more-product-info .price {
  color: #fff;
}
.more-product-info .product-info {
  font-size: 18px;
  margin: 4px 0;
  font-family: $font-primary;
}
.more-product-info .price {
  font-size: 1rem;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  margin-bottom: 0;
}
.last-product {
  position: relative;
}
.more-product-info-last img {
  display: block;
  margin-bottom: 20px;
}
.more-product-info-last {
  position: absolute;
  top: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  left: 50%;
  font-weight: 300;
  font-family: 'Source Sans Pro', sans-serif;
  color: #fff;
  line-height: normal;
  font-size: 1rem;
  border-left: 1px solid white;
}
a.more-product-info-last:hover {
  text-decoration: none;
  color: #fff;
}
.plus {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.9rem;
  height: 2.9rem;
  margin-bottom: 16px;
  background-color: #fff;
  border-radius: 50%;
  .icon-plus {
    width: 70%;
    height: 70%;
  }
}
// section Learn
.learn {
  padding: 6rem 0px;
  .content {
    margin-bottom: 2.5rem;
    max-width: 1000px;
    padding-left: 5%;
  }
  .border-btn-white {
    margin: 1.5rem auto 0;
  }
}

.border-btn-white {
  border-color: #1d363b;
  color: #1d363b;
  font-size: 16px;
}
.border-btn-white:hover {
  border-color: #1d363b;
}
.trusted-seller-icon {
  position: absolute;
  bottom: -7%;
  left: 42%;
  z-index: 999;
}
/* End of More Product */
/* Footer */
.footer {
  background: #f8f8f8;
  padding: 1rem 0;
}
.footer-newsletter {
  display: flex;
  align-items: center;
  margin: 1rem 0;
  @media only screen and (max-width: 992px) {
    flex-wrap: wrap;
  }
}
.footer-newsletter span {
  margin: 0.5rem 1.5rem 0.5rem 0;
  font-family: $font-secondary;
}
.newsletter-input {
  display: inline-block;
  position: relative;
}
.newsletter-input {
  width: 100%;
  max-width: 336px;
  display: inline-block;
}
.newsletter-input .btn {
  position: absolute;
  right: 1px;
  top: 1px;
  border-radius: 0px;
  width: 30px;
  height: 30px;
  padding: 0;
  line-height: 10px;
  width: 30px;
  height: 30px;
}
.newsletter-input .form-control {
  font-family: 'Muli', sans-serif;
  font-weight: 200;
  font-size: 14px;
  height: 32px;
  border-radius: 0px;
}
.newsletter-input .form-control:focus {
  border: 1px solid #1d363b;
}
.newsletter-input .form-control::-webkit-input-placeholder {
  color: #1d363b;
}
.newsletter-input .form-control::-moz-placeholder {
  color: #1d363b;
}
.newsletter-input .form-control:-ms-input-placeholder {
  color: #1d363b;
}
.newsletter-input .form-control:-moz-placeholder {
  color: #1d363b;
}
.footer-social {
  margin: 1rem 0;
  a:hover .icon {
    fill: grey;
  }
}
.footer-social .list-inline-item:not(:last-child) {
  margin-right: 1rem;
}
.footer-links a {
  color: #1d363b;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  font-size: 0.8rem;
}
.footer-links .list-inline-item:not(:last-child) {
  margin-right: 2em;
}
.footer__copyright {
  width: 100%;
  padding: 0.8rem 0 1rem;
  text-align: center;
  border-top: 1px solid #1d363b;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 12px;
  opacity: 0.5;
}
/* End of Footer */
